import Preact from 'preact'

const main = document.querySelector('main')

function el(name, props = {}, children = []) {
  const element = document.createElement(name)
  for (const prop in props) {
    element.setAttribute(prop, props[prop])
  }
  for (const child of children) {
    element.append(child)
  }
  return element
}

browser.runtime
  .sendMessage({
    message: 'popupWantsData'
  })
  .then(response => {
    console.log(response)
    if (response && response.data && response.data.version) {
      render(response.data.searches)
    }
  })

function saveData(data = {}) {
  browser.runtime.sendMessage({
    message: 'popupWantsToSave',
    data: data
  })
}

window.onblur = () => {
  saveData()
}

function render(searches) {
  const list = el(
    'div',
    {},
    searches.map(s => el('p', {}, s.name))
  )
  render(<div>hello</div>, main)
}
